  padding-top: 9em;
  padding-left: 5em;
  padding-bottom: 1em;
}

.landing-video-text-top-col {
  color: white;
  font-size: 3em;
  font-weight: 700;
  line-height: 1em;
}

.landing-video-text-middle {
  padding-left: 5em;
  padding-bottom: 3em;
}

.landing-video-text-middle-col {
  color: rgb(218, 195, 195);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-weight: 500;
  word-spacing: 0.1em;
  letter-spacing: 0.01em;
  /* filter: contrast(60%); */
}

.landing-video-text-middle-col .video-link-text {
  color: white;
  text-decoration: none;
  margin-right: 1.2em;
  border: 3px solid white;
  padding: 0.2em 0.6em;
  padding-bottom: 0.4em;
  border-radius: 50%;
  color: white;
  /* filter: contrast(100%); */
  /* font-weight: 300; */
}

.landing-video-text-third {
  padding-left: 5em;
  padding-bottom: 1em;
}

.landing-video-text-third-col-A {
  color: rgb(218, 195, 195);
  font-size: 0.85em;
  line-height: 1.3em;
  padding-top: 0;
  font-weight: 500;
  word-break: normal;
  word-wrap: break-word;
  padding-right: 10em;
}

.landing-video-text-third-col-B {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.landing-video-card {
  width: 13em;
  margin-right: 3em;
  height: 7em;
  position: relative;
  border: none;
}

.landing-video {
  position: absolute;
  object-fit: contain;
  height: 100%;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
  width: 100%;
  padding: 0;
}

.search-from-main {
  padding-bottom: 0.1em;
}

.search-label {
  font-weight: 300;
  font-size: 1.3em;
}

.input-location-box {
  border: none;
  background: transparent;
  border-bottom: 2px solid white;
  color: white;
  border-radius: 0;
  font-size: 1em;
  padding: 0.5em 0.2em;
}

.input-location-box:focus,
.input-location-box:active {
  background: transparent;
  color: white;
  outline: none !important;
  box-shadow: none;
}
.location-input-text {
  background: transparent;
  color: white;
  border: none;
  border-bottom: 2px solid white;
  border-radius: 0;
}

.location-date-group {
  display: flex;
  flex-direction: row;
}

.location-date-pick-class {
  border: none;
  background: transparent;
  border-bottom: 2px solid white;
  color: white;
  outline-width: 0;
  border-radius: 0;
  font-size: 1em;
  padding: 0.5em 0.2em;
}

.location-date-pick-class:focus,
.location-date-pick-class:active {
  background: transparent;
  color: white;
  outline: none !important;
  box-shadow: none;
  border-radius: 0;
}

.search-button-location,
.search-button-location:hover,
.search-button-location:focus,
.search-button-location:active {
  background: none;
  border: 0;
  outline: none !important;
  box-shadow: none !important;
  font-weight: 500;
  font-size: 1.2em;
}

.search-button-location:hover {
  background: none;
  border: none;
}

.booking-host-card {
  height: -webkit-max-content;
  height: max-content;
}

.booking-card-lines {
  background-color: white;
  color: black;
  border-radius: 3px;
}

.main-app {
  position: absolute;
  height: -webkit-max-content;
  height: max-content;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-backdrop-filter: brightness(45%);
          backdrop-filter: brightness(45%);
  background-image: url(/static/media/app-background.6b1c10db.jpg);
  /* background-color: (0,0,0,0.2); */
  background-position: 70% 30%;
  background-repeat: no-repeat;
}

.main-app.url-change {
  background-image: url(/static/media/place-background.13e4f3cd.png);
  background-size: 100% 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Roboto", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  object-fit: contain;
  padding: 0 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

